.user-page {
  margin-top: 35px;
  width: 100%;

  &__search-bar {
    display: flex;
  }

  &__error {
    margin-bottom: 20px;
    text-align: center;
    color: #ce3431;
    font-style: italic;
  }
}

.info {
  display: flex;
  flex-direction: row;
  margin-bottom: 25px;

  &__avatar {
    border-radius: 5px;
    width: 140px;
    height: 140px;
    object-fit: cover;
    margin-right: 15px;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    &__login {
      margin-bottom: 8px;
      color: #fff;
      cursor: pointer;
    }

    &__followers {
      margin-bottom: 8px;
      color: #fff;
      cursor: default;

      &-value {
        color: #8a57e5;
        font-weight: 500;
      }
    }

    &__following {
      margin-bottom: 8px;
      color: #fff;
      cursor: default;

      &-value {
        color: #398bf7;
        font-weight: 500;
      }
    }

    &__repo {
      margin-bottom: 0;
      color: #fff;
      cursor: default;

      &-value {
        font-weight: 500;
        color: #55ba51;
      }
    }
  }
}

